import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { ModalController } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';
import { Post, Collections, PostComment } from '../../app/app.models';
import { AuthProvider } from '../../providers/auth/auth';
import { take } from 'rxjs/operators';

@Component({
  selector: 'zamna-feed',
  templateUrl: 'zamna-feed.html'
})
export class ZamnaFeedComponent {

  /* public feed$: Observable<any[]> = this.back.loadFeed().pipe(map(feed => feed.filter(f => f.user))) */
  public feed$: Observable<Post[]> = this.afs.collection<Post>(Collections.POST).valueChanges()
  public loader: boolean = false

  constructor(
    private modal: ModalController,
    private afs: AngularFirestore,
    public auth: AuthProvider
  ) {
    
  }

  async openComments(post: Post) {
    this.loader = true
    const comments = await this.afs.collection<PostComment>(Collections.POST_COMMENT, ref => ref
      .where('post' ,'==', post.id))
      .valueChanges()
      .pipe(take(1))
      .toPromise()
    const m = this.modal.create('PostCommentsPage', {comments})
    m.present()
    this.loader = false
  }

  /* async toggleLike(post, uid: string, isLiked: boolean) {

    this.loader = true

    if (isLiked) {
      await this.back.removePostLike(post.id, uid).toPromise()
      post.likes.splice(post.likes.indexOf(uid), 1)
    } else {
      await this.back.addPostLike(post.id, uid).toPromise()
      post.likes.push(uid)
    }

    this.loader = false

  } */

  isLiked(post: Post): boolean {
    if (!this.auth.user) return false
    if (!post) return false
    if (!post.likes) return false
    return post.likes.indexOf(this.auth.user.uid) >= 0
  }

  toggleLike(post: Post, id: string, likes: string[] = []) {
    this.isLiked(post) ? likes.splice(likes.indexOf(this.auth.user.uid), 1) : likes.push(this.auth.user.uid)
    this.afs.doc(`${Collections.POST}/${id}`).update({likes})
  }

  removePost(id: string) {
    this.afs.doc(`${Collections.POST}/${id}`).delete()
  }

}



