import { Component, Input, OnInit } from '@angular/core';
import { Gallery, GalleryCategory, Collections } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { take } from 'rxjs/operators';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'gallery-item',
  templateUrl: 'gallery-item.html'
})
export class GalleryItemComponent implements OnInit {

  @Input() gallery: GalleryCategory
  public galleries: Gallery[] = []

  constructor(
    private afs: AngularFirestore,
    public navCtrl: NavController
  ) {}

  ngOnInit() {
    if (this.gallery) {
      this.loadGalleries(this.gallery.id)
    }
  }

  async loadGalleries(cat_id: string) {

    const galleries = await this.afs.collection<Gallery>(Collections.GALLERY, ref => ref
      .where('cat_id', '==', cat_id))
      .valueChanges()
      .pipe(take(1))
      .toPromise()

    this.galleries = galleries


  }

}



