import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Collections, Content } from '../../app/app.models';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage({ segment: 'materia/:id' })
@Component({
  selector: 'page-materia',
  templateUrl: 'materia.html',
})
export class MateriaPage {

  public information: any = []
  public loader: boolean = false

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private data: AuthProvider,
  ) {
  }

  ionViewDidLoad() {

    this.loader = true
    const id = this.navParams.get('id')
    this.loadContent(id)

    /* this.back.getTemario(this.navParams.get('id')).subscribe(data => {
      console.log(data)
      this.information = data.map(b => ({
        name: b.name,
        children: b.temas.map(t => ({
          id: t.id,
          name: t.name,
          information: t.desc,
          video: t.video,
          children: !t.video ? t.subs.map(s => ({
            id: s.id,
            name: s.name,
            information: s.desc,
            video: s.video})) : null
          }))
        }))
      this.loader = false
    }, err => {
      this.toast.create({message: 'No se pudo cargar el temario...', duration: 2000}).present()
      this.loader = false
      if (this.navCtrl.canGoBack()) this.navCtrl.pop()
    }) */

  }

  async loadContent(id: string) {

    let materia = await this.data.getDoc<Content>(Collections.CONTENT, id)
    materia.children = await this.data.getCollectionQuery<Content[]>(Collections.CONTENT, ref => ref
      .where('parent_id', '==', materia.id))

    materia.children = materia.children.map(m => ({...m, children: []}))

    // Load temas
    for (let bloque of materia.children) {

      bloque.children = await this.data.getCollectionQuery<Content[]>(Collections.CONTENT, ref => ref
        .where('parent_id', '==', bloque.id))

      // Load subtemas
      for (let tema of bloque.children) {
        tema.children = await this.data.getCollectionQuery<Content[]>(Collections.CONTENT, ref => ref
          .where('parent_id', '==', tema.id))
      }

    }

    this.loader = false
    this.information = materia.children
    console.log(materia)

  }

}



