import {
  NgModule
} from '@angular/core';
import {
  AccordionListComponent
} from './accordion-list/accordion-list';
import {
  CommonModule
} from '@angular/common';
import {
  IonicModule
} from 'ionic-angular';
import {
  TimerComponent
} from './timer/timer';
import {
  ZamnaFeedComponent
} from './zamna-feed/zamna-feed';
import { MomentModule } from 'angular2-moment';
import { GalleryItemComponent } from './gallery-item/gallery-item';
import { MediaItemComponent } from './media-item/media-item';
@NgModule({
  declarations: [AccordionListComponent,
    TimerComponent,
    ZamnaFeedComponent,
    GalleryItemComponent,
    MediaItemComponent
  ],
  imports: [CommonModule, IonicModule, MomentModule],
  exports: [AccordionListComponent,
    TimerComponent,
    ZamnaFeedComponent,
    GalleryItemComponent,
    MediaItemComponent
  ]
})
export class ComponentsModule {}



