import { Component, Input, OnInit } from '@angular/core';
import { MediaCategory, Media, Collections } from '../../app/app.models';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { AngularFirestore } from 'angularfire2/firestore';
import { take } from 'rxjs/operators';

@Component({
  selector: 'media-item',
  templateUrl: 'media-item.html'
})
export class MediaItemComponent implements OnInit {

  @Input() public media: MediaCategory
  public medias: Media[] = []

  constructor(
    private iab: InAppBrowser,
    private afs: AngularFirestore
  ) {
    
  }

  ngOnInit() {
    this.loadMedias(this.media.id)
  }

  openMedia(url: string) {
    //this.iab.create(this.back.API + url.replace('/file', '/fileNew'), '_system')
    this.iab.create(url, '_system')
  }

  async loadMedias(cat_id: string) {

    const medias = await this.afs.collection<Media>(Collections.MEDIA, ref => ref
      .where('categoria.id', '==', cat_id))
      .valueChanges()
      .pipe(take(1))
      .toPromise()

    this.medias = medias

  }

}



